import React, { Component } from "react";
import _ from "lodash";
import Ajax from "Pub/js/ajax";
import "./index.less";
import withDragDropContext from "Pub/js/withDragDropContext";
//
import MySider from "./sider";
import MyContent from "./content";
import { GetQuery } from "Pub/js/utils";
import { connect } from "react-redux";
import * as utilService from "./utilService";
import {
    updateGroupList,
    clearData,
    initGroups,
    setJson
} from "Store/test/action";
import Notice from "Components/Notice";
import CustomDragLayer from "./customDragLayer";
import { getMulti } from "Pub/js/getMulti";
import platform from "Pub/js/platform";
const { NCDiv } = platform.base;
class Test extends Component {
    constructor(props) {
        super(props);
        //url
        const urlRequestObj = GetQuery(this.props.location.search);
        const relateidObj = utilService.getRelateidObj(
            urlRequestObj.pk_responsibility,
            urlRequestObj.is_group
        );
        this.state = {
            relateidObj: relateidObj
        };
    }
    //redux
    componentWillUnmount() {
        this.props.clearData();
        window.onbeforeunload = null;
        // window.removeEventListener("beforeunload", this.loadPage);
    }
    // beforeunload = () => {
    // 	//
    // 	window.onbeforeunload = function (e) {
    // 		e = e || window.event;
    // 		var y = e.clientY;
    // 		if (y <= 0 || y >= Math.max(document.body ? document.body.clientHeight : 0, document.documentElement ? document.documentElement.clientHeight : 0))
    // 		{
    // 			//IE  Firefox
    // 			// alert("IE or Firefox");
    // 			e.returnValue = "";
    // 		}
    // 		//
    // 		// console.log("beforeclosing");
    // 		return "";
    // 	}
    // }

    loadPage = e => {
        var confirmationMessage = "";
        (e || window.event).returnValue = confirmationMessage; //Gecko + IE
        return confirmationMessage; //Webkit, Safari, Chrome etc.
    };
    componentWillMount() {
        window.onbeforeunload = this.loadPage; //dom2windows
    }
    componentDidMount() {
        // window.addEventListener("beforeunload", this.loadPage);
        let callback = json => {
            // console.log(json);
            this.props.setJson(json);
        };
        getMulti({
            moduleId: "DesktopSetting",
            // currentLocale: 'zh-CN',
            domainName: "workbench",
            callback
        });

        let ajaxData = {
            isuser: this.state.relateidObj.code //0 1 2
        };
        if (this.state.relateidObj.code === "0") {
            ajaxData.relateid = this.state.relateidObj.data;
        }
        let queryUrl = this.props.location && this.props.location.search;
        if (queryUrl && queryUrl.indexOf("pk_responsibility") !== -1) {
            this.state.relateidObj.code = "0"; //
            this.setState({
                relateidObj: {
                    ...this.state.relateidObj,
                    code: "0"
                }
            });
            ajaxData.isuser = "0";
            let querySubString = queryUrl.substring(
                queryUrl.indexOf("pk_responsibility")
            );
            let relateid = querySubString.substring(
                querySubString.indexOf("=") + 1
            );
            ajaxData.relateid = relateid; //relateid
        } else if (queryUrl && queryUrl.indexOf("is_group=true") !== -1) {
            //  2019-08-16
            ajaxData.isuser = "2";
        }
        Ajax({
            url: `/nccloud/platform/appregister/queryworkbench.do`,
            info: {
                name: this.props.json[
                    "DesktopSetting-000004"
                ] /*  */,
                action: this.props.json[
                    "DesktopSetting-000024"
                ] /*  */
            },
            data: ajaxData,
            success: res => {
                if (res) {
                    let { data, success } = res.data;
                    if (success && data && data.length > 0) {
                        //
                        _.forEach(data[0].groups, g => {
                            g.type = "group";
                            _.forEach(g.apps, a => {
                                a.isShadow = false;
                                a.isChecked = false;
                                a.apptype = Number(a.apptype);
                                a.gridx = Number(a.gridx);
                                a.gridy = Number(a.gridy);
                                a.height = Number(a.height);
                                a.width = Number(a.width);
                            });
                        });
                        this.props.updateGroupList(data[0].groups);
                        this.props.initGroups(data[0].groups);
                    } else {
                        if (success && data && data.length === 0) {
                            // Notice({ status: 'warning', msg: this.props.json['DesktopSetting-000025'] });/*  */
                        } else {
                            Notice({ status: "error", msg: data });
                        }
                    }
                }
            }
        });
    }

    render() {
        return [
            <NCDiv areaCode={NCDiv.config.HEADER}>
                <NCDiv
                    fieldid={
                        this.props.json[
                            "DesktopSetting-000043"
                        ] /*  */
                    }
                    areaCode={NCDiv.config.Title}
                />
            </NCDiv>,
            <div className="nc-desktop-setting">
                <MySider relateidObj={this.state.relateidObj} {...this.props} />
                <MyContent
                    relateidObj={this.state.relateidObj}
                    unLoad={this.loadPage}
                />
                {/*  */}
                <CustomDragLayer />
            </div>
        ];
    }
}
export default connect(
    state => ({
        json: state.templateDragData.json
        //redux
    }),
    {
        updateGroupList,
        clearData,
        initGroups,
        setJson
        //redux
    }
)(withDragDropContext(Test));
